<template>
    <div class="debug" ref="mc"></div>
    <div v-if="isUserActionRequired" id="startScreen"><button @click="Entry()">START AR</button></div>
</template>

<script>
    import * as THREE from 'three';
    import * as AR_Renderer from "@/AR-Renderer/ar_renderer";


    export default {
        name: 'MarkerScene',
        props: {
            sceneData: Object
        },
        data() {
            return {
                isUserActionRequired: false,
            };
        },
        mounted() {
            this.Entry();
        },
        methods: {
            async Entry(loadDef) {
                if (loadDef == 1) {
                    window.ShowOrientationScreen(true);
                } else {
                    window.ShowLoadingIndicator(true);
                }
                await window.SetCurrentScene(this);
                this.Launch(loadDef);
            },

            async Launch(loadDef) {
                await this.Setup();
                await this.Start();
                if (loadDef == 1) {
                    window.ShowOrientationScreen(false);
                } else {
                    window.ShowLoadingIndicator(false);
                }
                return;
            },

            async Setup() {
                this.scene = new THREE.Scene();
                this.camera = new THREE.PerspectiveCamera(70, window.innerWidth / window.innerHeight, 0.01, 20);

                this.renderer = new THREE.WebGLRenderer({ antialias: true, alpha: true });
                this.renderer.setPixelRatio(window.devicePixelRatio);
                this.renderer.setSize(window.innerWidth, window.innerHeight);
                this.renderer.xr.enabled = true;

                this.$refs.mc.appendChild(this.renderer.domElement);

                this.controller = this.renderer.xr.getController(0);
                this.scene.add(this.controller);

                this.Start = async () => {
                    navigator.xr.requestSession('immersive-ar', {
                        optionalFeatures: [
                            "dom-overlay"
                        ],
                        requiredFeatures: [
                            "hit-test",
                        ],
                        domOverlay: {
                            root: this.sceneData.unity
                        }
                    }).then(async (session) => {
                        this.isUserActionRequired = false;
                        this.session = session;
                        this.renderer.xr.setReferenceSpaceType('local');
                        await this.renderer.xr.setSession(session);
                        AR_Renderer.onSceneLoaded(this.sceneData.sceneIndex);
                        this.animate();
                        return;
                    }).catch(()=>{
                        this.isUserActionRequired = true;
                    });
                }

                this.Stop = async () => {
                    if (this.session) {
                        await this.session.end();
                        return;
                    }
                    return;
                }
            },
            animate() {
                this.renderer.setAnimationLoop(() => {
                    this.renderer.render(this.scene, this.camera);
                    this.SendCameraTransformTounity();
                });
            },

            SendCameraTransformTounity() {
                let matrix = this.camera.matrixWorld.elements;
                let fov = this.camera.fov;
                AR_Renderer.onMarkerTracking(matrix, fov);
            }
        }
    }
</script>

<style scoped>
    @import '../css/styles.css';

    .debug {
        width: 100%;
        height: 100%;
    }

    #startScreen {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        backdrop-filter: blur(10px);
        justify-content:flex-end;
        align-items: center;
        flex-direction: column;
        padding: 50px;
        display: flex;
        z-index: 1;
    }

        #startScreen button {
            padding: 10px;
            background-color: transparent;
            border-radius: 10px;
            font-size: 14px;
            color: darkgrey;
            font-weight: bold;
            border: 1px solid darkgrey;
        }

            #startScreen button:active {
                color: lightgrey;
                border: 2px solid lightgrey;
            }
</style>
